import { LiteGraph } from 'litegraph.js';
import { graph } from '../App';

//   
export const handleMountConstantNumberBlock = () => {
  const node_constNumber = LiteGraph.createNode('basic/constNumber'); //     
  graph.add(node_constNumber); //     ,    
};

