import { useEffect, useState } from 'react';
import nodeStyles from './nodes/nodeStyles';
import AddNode from './nodes/AddNode'; // ,   ,       ( )
import SubtractNode from './nodes/SubtractNode'; // ,   ,       ( )
import DivideNode from './nodes/DivideNode'; // ,   ,       ( )
import MultiplyNode from './nodes/MultiplyNode'; // ,   ,       ( )
import ResultNode from './nodes/ResultNode'; // ,   ,       ( )
import ConstantNumber from './nodes/ConstNumberNode'; // ,   ,       ( )
import StartNode from './nodes/StartNode'; // ,   ,       ( )
import TimerNode from './nodes/TimerNode'; // ,   ,       ( )
import { LGraph, LiteGraph, LGraphCanvas } from 'litegraph.js';

// 
import Header from './Header';
import Main from './Main';
// import Footer from './Footer';

nodeStyles(); //    
export const graph = new LGraph(); //  
const canvas = new LGraphCanvas('#mycanvas', graph); //     html-  graph  

//   
// canvas.background_color = '#fafafa';
// canvas.background_image = 'data:image/png;base64,   base64  ';

//       
LGraphCanvas.prototype.showSearchBox = function () {
  return false;
};

//     
LGraphCanvas.prototype.processContextMenu = function () {
  return false;
};

console.log(LiteGraph.registered_node_types); //    
function App() {
  const [isSideMenuPropertiesOpen, setSideMenuPropertiesOpen] = useState(false); //    
  const [selectedNode, setSelectedNode] = useState(null);
  //      -  
  canvas.onNodeDeselected = function () {
    setSideMenuPropertiesOpen(false);
    setSelectedNode(null); //    (  )   SideMenu
  };

  //      SideBar
  useEffect(() => {
    const getProperties = function (node) {
      setSelectedNode(node); //     
      setSideMenuPropertiesOpen(true);
      console.log(node);
    };
    canvas.onNodeSelected = getProperties;
    //      
    return () => {
      canvas.onNodeSelected = null;
    };
  }, []); //      

  //        
  useEffect(() => {
    function resizeCanvas() {
      canvas.resize(window.innerWidth, window.innerHeight); //      
      canvas.draw(true); //  
    }
    //   resizeCanvas        
    window.onload = resizeCanvas;
    window.onresize = resizeCanvas;
    //   
    return () => {
      window.onload = null;
      window.onresize = null;
    };
  }, []);

  return (
    <div className="App">
      <Header graph={graph} />
      <Main
        selectedNode={selectedNode}
        isSideMenuPropertiesOpen={isSideMenuPropertiesOpen}
        setSideMenuPropertiesOpen={setSideMenuPropertiesOpen}
      />
      {/* <Footer /> */}
    </div>
  );
}

export default App;
