import { useState } from 'react';
import { LGraph } from 'litegraph.js';
import { Container } from '@mui/system';
import { lightGreen } from '@mui/material/colors';
import SideMenuFunctions from './SideMenuFunctions';
import {
  Menu as MenuIcon,
  Stop as StopIcon,
  Download as DownloadIcon,
  PlayArrow as PlayArrowIcon,
  CloudUpload as CloudUploadIcon,
  CloudDownload as CloudDownloadIcon,
} from '@mui/icons-material';
import { AppBar, Button, Toolbar, Typography, Box, Menu, Tooltip, MenuItem, IconButton } from '@mui/material';

const options = ['', '']; //    (AppBar) -   ,    ,  
const fileFeatures = [' ', ' ', ' ']; // ,    
const accauntFeatures = ['', '']; // ,    

function Header({ graph }) {
  const [isOpenUserFeatures, setOpenUserFeatures] = useState(null); //      
  const [isOpenFileFeatures, setOpenFileFeatures] = useState(null); //      
  const [isSideMenuFunctionsOpen, setSideMenuFunctionsOpen] = useState(false); //    
  const [inProgress, setInProgress] = useState(false); //     

  // ,    
  const handleOpenUserMenu = (event) => {
    setOpenUserFeatures(event.currentTarget);
  };
  const handleCloseUserMenu = () => {
    setOpenUserFeatures(null);
  };

  // ,    
  const handleOpenFileMenu = (event) => {
    setOpenFileFeatures(event.currentTarget);
  };
  const handleCloseFileMenu = () => {
    setOpenFileFeatures(null);
  };

  //    (SideMenuFunctions)
  const handleOpenSideMenuFunctions = () => {
    setSideMenuFunctionsOpen(true);
  };

  //  
  const handleStart = () => {
    graph.start(); //   
    console.log('Start');
    setInProgress(true);
    LGraph.status = LGraph.STATUS_RUNNING; // 2
  };
  //  
  const handleStop = () => {
    graph.stop(); //   
    console.log('Stop');
    setInProgress(false);
    LGraph.status = LGraph.STATUS_STOPPED; // 1
  };

  //    localStorage
  function saveGraph() {
    const data = graph.serialize();
    const jsonStr = JSON.stringify(data);
    localStorage.setItem('graphData', jsonStr);
  }
  //    localStorage
  function loadGraph() {
    const data = localStorage.getItem('graphData');
    if (data) {
      graph.configure(JSON.parse(data));
    }
  }
  //    localStorage
  function downloadGraph() {
    const data = graph.serialize();
    const jsonStr = JSON.stringify(data);
    const blob = new Blob([jsonStr], { type: 'text/plain;charset=utf-8' });
    const link = document.createElement('a');
    link.href = URL.createObjectURL(blob);
    link.download = 'graph.json';
    link.click();
  }

  return (
    <AppBar position="fixed">
      <Container maxWidth="x2">
        <Toolbar disableGutters>
          <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="menu"
            sx={{ mr: 2 }}
            onClick={handleOpenSideMenuFunctions}>
            <MenuIcon />
          </IconButton>

          {/*   */}
          <Box sx={{ flexGrow: 0 }}>
            <Tooltip>
              <Button color="inherit" variant="text" onClick={handleOpenFileMenu}>
                
              </Button>
            </Tooltip>
            <Menu
              sx={{ mt: '45px' }}
              id="menu-appbar"
              anchorEl={isOpenFileFeatures}
              anchorOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              open={Boolean(isOpenFileFeatures)}
              onClose={handleCloseFileMenu}>
              {fileFeatures.map((feature) => (
                <MenuItem key={feature} onClick={handleCloseFileMenu}>
                  <Typography textAlign="center">{feature}</Typography>
                </MenuItem>
              ))}
            </Menu>
          </Box>

          {/*  */}
          <Box sx={{ display: { xs: 'none', md: 'flex' } }}>
            {options.map((page) => (
              <Button key={page} sx={{ my: 2, color: 'white', display: 'block', p: 0, margin: 0, pl: 2 }}>
                {page}
              </Button>
            ))}
          </Box>

          {/* ,   localStorage   JSON  */}
          <Box sx={{ display: { xs: 'none', md: 'flex' }, ml: 5 }}>
            <Tooltip title=" ">
              <IconButton size="large" aria-label=" " color="inherit" onClick={saveGraph}>
                <CloudDownloadIcon />
              </IconButton>
            </Tooltip>
            <Tooltip title=" ">
              <IconButton size="large" aria-label=" " color="inherit" onClick={loadGraph}>
                <CloudUploadIcon />
              </IconButton>
            </Tooltip>
            <Tooltip title=" JSON ">
              <IconButton size="large" aria-label=" JSON " color="inherit" onClick={downloadGraph}>
                <DownloadIcon />
              </IconButton>
            </Tooltip>
          </Box>

          {/*     */}
          <Box sx={{ flexGrow: 1 }} />
          <Box sx={{ display: { xs: 'none', md: 'flex' }, mr: 2 }}>
            <Tooltip title=" ">
              <IconButton size="large" aria-label=" " color="inherit" onClick={handleStart}>
                <PlayArrowIcon sx={{ color: inProgress ? lightGreen[500] : '' }} />
              </IconButton>
            </Tooltip>
            <Tooltip title=" ">
              <IconButton size="large" aria-label=" " color="inherit" onClick={handleStop}>
                <StopIcon />
              </IconButton>
            </Tooltip>
          </Box>

          {/*   */}
          <Box sx={{ flexGrow: 0 }}>
            <Tooltip title=" ">
              <Button color="inherit" variant="outlined" onClick={handleOpenUserMenu}>
                
              </Button>
            </Tooltip>
            <Menu
              sx={{ mt: '45px' }}
              id="menu-appbar"
              anchorEl={isOpenUserFeatures}
              anchorOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              open={Boolean(isOpenUserFeatures)}
              onClose={handleCloseUserMenu}>
              {accauntFeatures.map((feature) => (
                <MenuItem key={feature} onClick={handleCloseUserMenu}>
                  <Typography textAlign="center">{feature}</Typography>
                </MenuItem>
              ))}
            </Menu>
          </Box>
        </Toolbar>
      </Container>
      <SideMenuFunctions menuOpen={isSideMenuFunctionsOpen} closeMenu={() => setSideMenuFunctionsOpen(false)} />
    </AppBar>
  );
}

export default Header;
